import { mapState } from 'vuex';
export default {
  props: ['item'],
  data() {
    return {
      confirm: false
    };
  },
  computed: mapState('admin', {
    loading: state => state.loadings.couponDelete
  }),
  methods: {
    async deleteProduct() {
      await this.$store.dispatch('admin/deleteCoupon', this.item.id);
    }
  }
};
