export default {
  props: ['location'],
  data() {
    return {
      current: 6
    };
  },
  computed: {
    page: {
      get() {
        return this.pagination.current_page;
      },
      set(val) {
        const patch =
          this.location == 'products' ? 'admin/getProducts' : 'admin/getOrders';
        this.$store.dispatch(patch, val);
      }
    },
    pagination() {
      return this.$store.state.admin.pagination[this.location];
    }
  }
};
