export default {
  computed: {
    drawer: {
      get() {
        return this.$store.state.admin.drawer;
      },
      set(val) {
        this.$store.commit('admin/updateDrawer', val);
      }
    },
    menuList() {
      return this.$store.state.admin.menuList;
    }
  }
};
