import { mapState } from 'vuex';
export default {
  props: ['id', 'num'],
  computed: mapState('front', {
    loading: state => state.loadings.addToCart
  }),
  methods: {
    async addToCart() {
      this.$store.dispatch('front/addToCart', [this.id, this.num]);
    }
  }
};
