import { mapState, mapActions } from 'vuex';
export default {
  data() {
    return { coupon: '' };
  },
  computed: {
    ...mapState('front', ['cartData']),
    ...mapState('front', {
      loading: (state) => state.loadings.deleteCart,
    }),
  },
  methods: mapActions('front', ['getCarts', 'deleteCart', 'addCouponCode']),
  created() {
    this.getCarts();
  },
};
